<template>
    <div v-if="ready">
        <div class='col-12 px0' style="display:flex;flex-direction:row;">
            <div class='col-3' style='padding-left:0;margin-bottom:1em'>
                <label>Department</label>
                <select class='form-control select' v-model="dept" @change="fetch_info">
                    <option v-for="d in dept_list" :value="d.dept_id">{{ d.full_dept }}</option>
                </select>
            </div>
            <div class='col-5'></div>
            <div class='col-2'>
                <label>Date</label>
                <input type="date" v-model="dd" class="form-control" @change="update_period()"/>
            </div>
            <!--
            <div class='col-2'>
                <label>School Year</label>
                <input type="number" v-model="st" class="form-control" @change="update_period"/>
            </div>
            <div class='col-2'>
                <label>&nbsp;</label>
                <input type="number" v-model="ed" class="form-control" readonly/>
            </div>
            -->
            <div class="col-2" style='padding-right:0'>
                <label>Live Search</label>
                <input type="text" class="form-control" placeholder="Enter Search Key" v-model="key" @keyup="fetch_info"/>
            </div>
        </div>
        <table class="table table-striped first-td-padding">
            <thead>
                <tr>
                    <th>Tag</th>
                    <th>Procurement ID</th>
                    <th>Asset Category</th>                   
                    <th>Item</th>
                    <th>Fund Source</th>
                    <th>Amount</th>
                    <th>Fund Balance</th>
                    <th>Asset Balance</th>
                    <th>Support Document</th>
                    <th>Status</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="l in data.length">
                    <td class="redirect" @click="view_reimb(data[l-1].tag)">{{ data[l-1].tag.toUpperCase() }}</td>
                    <td :class="check_class(data[l-1].proc_id)" @click="view_proc(data[l-1].proc_id)">{{ data[l-1].proc_id }}</td>
                    <td>{{ data[l-1].asset_cat }}</td>
                    <td>
                        <div class="inner_item" v-for="n in data[l-1].item.length">
                            {{ data[l-1].item[n-1] }}
                        </div>
                    </td>
                    <td>{{ data[l-1].fundsrc }}</td>
                    <td>
                        <div class="inner_item" v-for="n in data[l-1].amount.length">
                            {{ data[l-1].amount[n-1] }}
                        </div>
                    </td>
                    <td>{{ data[l-1].fund_bal }}</td>
                    <td>{{ data[l-1].asset_fund_bal }}</td>
                    <td>
                        <div v-for="p in data[l-1].pdf.length">
                            <a :href="data[l-1].pdf[p-1]">&bull;<u>{{ data[l-1].pdf_name[p-1] }}</u></a>                       
                        </div>
                    </td>
                    <td :class="data[l-1].status">{{ data[l-1].status }}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
    data(){
        return {
            dd:'',
            st:'',
            ed:'',
            dept:'',
            key:'',
            ready:false,
            user_check:false,
            dept_ready:false,
            dept_list:[],
            data:[],
        }
    },
    async created(){
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();

        today = yyyy + '-' + mm + '-' + dd;
        this.dd = today;
        await axios.get('Procurement/shared_backend/check_user_setup.php')
            .then(
                (response)=>{
                    this.user_check = response.data[0].signed;
                    if(!(this.user_check=='set')){
                        this.$router.push('../user_setup');
                    }
                }
            )
        await axios.get('Procurement/shared_backend/fetch_dept.php')
            .then(
                (response)=>{
                    this.dept_list = response.data;
                    this.dept = this.dept_list[0].dept_id;
                    this.dept_ready = true;             
                }
            )
        await axios.get('Procurement/shared_backend/default_period.php')
            .then(
                (response)=>{
                    var st = response.data.st.split("-");
                    var ed = response.data.ed.split("-");
                    this.st = st[0];
                    this.ed = parseInt(ed[0]);
                }
            )
        await this.fetch_info();
        this.ready = true;
    },
    methods:{
        async update_period(){
            var year = this.dd.substring(0,4);
            var month = this.dd.substring(5, 7);
            var day = this.dd.substring(8,10);
            if(month < 8){
                this.st = parseInt(year)-1;
                this.ed = parseInt(this.st)+1;
            } else if(month > 7){
                this.st = parseInt(year);
                this.ed = parseInt(this.st)+1;
            }
            this.$forceUpdate();
            await this.fetch_info();
        },
        check_class(id){
            if(id!=''){
                return 'hv_proc';
            } else {
                return 'no_proc';
            }
        },
        view_proc(proc_id){
            document.getElementById('tmp_id').value = proc_id;
            document.getElementById('st_tmp').value = this.st;
            document.getElementById('ed_tmp').value = this.ed;
            document.getElementById('doc_type').value = 'Procurement';
            this.$router.push('../wrapper/form_view');
        },
        view_reimb(tag){
            document.getElementById('tmp_id').value = tag;
            var st_val = this.st;
            //var st_array = st_val.split('-');
            //var st_month = st_array[0]+'-'+st_array[1];
            document.getElementById('st_tmp').value = st_val;

            var ed_val = this.ed;
            //var ed_array = ed_val.split('-');
            //var ed_month = ed_array[0]+'-'+ed_array[1];
            document.getElementById('ed_tmp').value = ed_val;
            document.getElementById('doc_type').value = 'Reimbursement';
            this.$router.push('../wrapper/form_view');
        },
        async fetch_info(){
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('key',this.key);
            formData.append('dd',this.dd);
            axios.post('Procurement/report/backend/reimb_fetch_json.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                            this.$forceUpdate();
                        } else {
                            this.data = [];
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
    .no_proc{
        pointer-events: none;
    }
    .redirect,.hv_proc{
        transition: 0.3s;
        cursor:pointer;       
    }
    .redirect:hover, .hv_proc:hover{
        background-color: rgba(183, 245, 181,0.95);
    }
    th{
        vertical-align:top!important;
        font-size:14px!important;
        border:1px solid white;
    }
    td{
        font-size:14px!important;
        border:1px solid white;
    }
    .px0{
        padding-left:0;
        padding-right:0;
    }
    .rejected,.cancelled{
        color:red;
        text-transform: capitalize;
    }
    .approved{
        color:green;
        text-transform: capitalize;
    }
    .pending{
        text-transform: capitalize;
    }
</style>


